import React, { Component } from 'react'

class AddWrestlersToEventTable extends Component {
    getWrestlerSelect(event){
        event.target.addClass = 'red';
    }
    render() {
        return (
            <div>
                <section className="pt-100">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-4">
                                <div className="wrestler-list-title text-center">
                                    <p>Wrestler List</p>
                                </div>
                                <div className="wrestler-list">
                                    <p>Wrestler Name</p>
                                    <ul>
                                        <li onClick={this.getWrestlerSelect.bind(this)}>Michael Folts</li>
                                        <li>Mohaiman</li>
                                        <li>Topu</li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-xl-4 text-center pt-200">
                                <div className="add-to-event">
                                    <button>Add to Event <i className="fas fa-angle-double-right"></i></button>
                                </div>
                                <div className="remove-to-event">
                                    <button> <i className="fas fa-angle-double-left"></i> Remove From Event</button>
                                </div>
                            </div>
                            <div className="col-xl-4">
                                <div className="wrestler-list-title text-center">
                                    <p>Wrestler In List</p>
                                </div>
                                <div className="wrestler-list">
                                    <p>Wrestler Name</p>
                                    <ul>
                                        <li>Michael Folts</li>
                                        <li>Mohaiman</li>
                                        <li>Topu</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default AddWrestlersToEventTable;