import React, { Component } from "react";

class WrestlerTeamCoachesList extends Component {
  render() {
    return (
      
        <div className="col-xl-6">
          <div className="coaches">
            <div className="coaches-title">
              <h1>
                <img
                  src={require("../../assets/images/icon_coach.svg")}
                  alt=""
                />{" "}
                Coaches
              </h1>
            </div>
          </div>
          <div className="coaches-list">
            <ul>
              <li>
                <img src={require("../../assets/images/jhon-doe.jpg")} alt="" />{" "}
                <span>#1: Mike Folts</span>
              </li>
              <li>
                <img src={require("../../assets/images/jhon-doe.jpg")} alt="" />{" "}
                <span>#2: Mark Foley</span>
              </li>
              <li>
                <img src={require("../../assets/images/jhon-doe.jpg")} alt="" />{" "}
                <span>#3: Leith Stetson</span>
              </li>
            </ul>
          </div>
        </div>
      
    );
  }
}

export default WrestlerTeamCoachesList;
