import React, { Component } from 'react'
import Header from '../components/Header'
import WrestlerTitle from '../components/WrestlerComponents/WrestlerTitle'
import WrestlerContext from '../components/WrestlerComponents/WrestlerContext'
import WrestlerTable from '../components/WrestlerComponents/WrestlerTable'

class Wrestler extends Component {
    render() {
        return (
            <div>
                <Header/>
                <WrestlerTitle/>
                <WrestlerContext/>
                <WrestlerTable/>
            </div>
        )
    }
}

export default Wrestler;