import React, { Component } from 'react'
import "../assets/css/sideNav.css";
import $ from 'jquery'; 

class SideNav extends Component {
    // constructor(props){
    //     super(props)
    //     this.onClose = this.onClose.bind(this)
    // }

    componentDidMount() {
        $('.closebtn').click(function(){
            $('#mySidenav').hide();
        });
    }
    render() {
        return (
            <div>
                <div id="mySidenav"  className="sidenav">
                    <a href="#" className="closebtn" >&times;</a>
                    <div className="user-image-name">
                        <ul>
                            <li><img src={require('../assets/images/jhon-doe.jpg')} alt=""/><span>Mike Folts</span></li>
                        </ul>
                        <div className="sub-image-menu">
                            <ul>
                                <li><a href="">Team Name</a></li>
                                <li><a href="">Help</a></li>
                                <li><a href="">Support</a></li>
                                <li><a href="" data-toggle="modal" data-target=".dia17-logout">Logout</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className="others-menu">
                        <ul>
                            <li><a href="#" data-toggle="modal" data-target=".dia8-edit-coaches"><img src={require('../assets/images/icon_coach.svg')} alt=""/><span>Edit Coaches</span></a></li>
                            <li><a href="#" data-toggle="modal" data-target=".dia6-add-match"><img src={require('../assets/images/icon_match.svg')}  alt=""/><span>Add Match</span></a></li>
                            <li><a href="#" data-toggle="modal" data-target=".dia7-add-wrestler"><img src={require('../assets/images/icon_wrestlers.svg')} alt="" /><span>Add Wrestler</span></a></li>
                            <li><a href="#" data-toggle="modal" data-target=".dia1-add-practics"><img src={require('../assets/images/icon_practice.svg')} alt=""/><span>Add Practice</span></a></li>
                            <li><a href="#" data-toggle="modal" data-target=".dia5-edit-event"><img src={require('../assets/images/icon_event.svg')} alt=""/><span>Add Event</span></a></li>
                            <li><a href="#" data-toggle="modal" data-target="#exampleModal"><img src={require('../assets/images/icon_team.svg')} alt=""/><span>Edit Team</span></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default SideNav;
