import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./App.css";
//import assets
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "./assets/css/style.css";

//import components
import NotFound from "./NotFound";
import Login from "./pages/Login";
import AdminLogin from "./pages/AdminLogin";
import TeamMemberLogin from "./pages/TeamMemberLogin";
import DirectorLogin from "./pages/DirectorLogin";
import EventWorkerLogin from "./pages/EventWorkerLogin";
import CreateAccount from "./pages/CreateAccount";
import AddWrestlersToEvent from "./pages/AddWrestlersToEvent";
import WrestlersTeam from "./pages/WrestlersTeam";
import Help from "./pages/Help";
import SearchForEvent from "./pages/SearchForEvent";
import ViewEvent from "./pages/ViewEvent";
import Wrestler from "./pages/Wrestler";
import PracticeSchedule from "./pages/PracticeSchedule";
import TeamPracticeAttendance from "./pages/TeamPracticeAttendance";
import WrestlerAttandance from "./pages/WrestlerAttandance";
import EventWorker from "./pages/EventWorker";
import WrestlerPage from "./pages/WrestlerPage";


function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Login} />
        <Route path="/admin-login" component={AdminLogin} />
        <Route path="/team-member-login" component={TeamMemberLogin} />
        <Route path="/director-login" component={DirectorLogin}/>
        <Route path="/event-worker-login" component={EventWorkerLogin}/>
        <Route path="/create-account" component={CreateAccount}/>
        <Route path="/add-wrestlers-to-event" component={AddWrestlersToEvent}/>
        <Route path="/wrestlers-team" component={ WrestlersTeam }/>
        <Route path="/search-for-event" component={ SearchForEvent }/>
        <Route path="/view-event" component={ ViewEvent } />
        <Route path="/wrestler" component={ Wrestler }/>
        <Route path="/practice-schedule" component={PracticeSchedule}/>
        <Route path="/team-practice-attendance" component={ TeamPracticeAttendance }/>
        <Route path="/wrestler-attendance" component={ WrestlerAttandance } />
        <Route path="/event-worker" component={ EventWorker} />
        <Route path="/wrestler-page" component={ WrestlerPage} />
        <Route path="/help" component={ Help }/>
        <Route path="" component={NotFound} />   
      </Switch>
    </Router>
  );
}

export default App;
