import React, { Component } from "react";
import { Link } from 'react-router-dom';

class WrestlerTeamEventList extends Component {
  render() {
    return (
      <div className="col-xl-6">
        <div className="events">
          <div className="event-title">
            <h1>
              <span>
                <img src={require('../../assets/images/icon_event.svg')} alt="" />
              </span>
              Events
            </h1>
            <div className="event-search text-right">
              <div className="input-group mb-3">
                <div className="input-group-append">
                <Link to="/search-for-event">
                <button
                    className="btn btn-outline-secondary"
                    type="button"
                  >
                    Search
                  </button>
                </Link>
              
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="event-list">
          <ul>
            <li>
              <a href="FRM3.html">
                Wrestling Match One <span>01/10/19</span>
              </a>
            </li>
            <li>
              <a href="FRM3.html">
                Wrestling Tournament <span>2/15/19</span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default WrestlerTeamEventList;
