import React, { Component } from 'react'
import Header from '../components/Header'
import PageTitle from '../components/PageTitle'
import SearchForEventForm from '../components/SearchForEventPage/SearchForEventForm'
import SearchForEventTable from '../components/SearchForEventPage/SearchForEventTable'

class SearchForEvent extends Component {
    render() {
        return (
            <div>
                <Header/>
                <PageTitle pageName='Search For Event'/>
                <SearchForEventForm/>
                <SearchForEventTable/>
            </div>
        )
    }
}

export default SearchForEvent;
