import React, { Component } from "react";
import WrestlerTeamCoachesList from "./WrestlerTeamCoachesList";
import WrestlerTeamEventList from "./WrestlerTeamEventList";

class WrestlerTeamEventCoachesList extends Component {
  render() {
    return (
      <div>
        <section className="coaches-events">
          <div className="container">
            <div className="row pt-100">
                <WrestlerTeamCoachesList/>
                <WrestlerTeamEventList/>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default WrestlerTeamEventCoachesList;
