import React, { Component } from 'react';
import Header from '../components/Header';
import PageTitle from '../components/PageTitle';
import PracticeScheduleTable from '../components/PracticeScheduleComponents/PracticeScheduleTable';
import PracticeScheduleTableDialog from '../components/PracticeScheduleComponents/PracticeScheduleTableDialog';


class PracticeSchedule extends Component {
    render() {
        return (
            <div>
                <Header/>
                <PageTitle pageName='Practice Schedule'/>
                <PracticeScheduleTable/>
                <PracticeScheduleTableDialog/>
            </div>
        )
    }
}


export default PracticeSchedule;