import React, { Component } from 'react'

class WrestlerContext extends Component {
    render() {
        return (
            <div>
                <section class='wrestler-content pt-50'>
                    <div class="container">
                        <div class="row">
                            <div class="col-xl-4">
                                <div class="wresteler-event-name-location">
                                    <div class="event-list">
                                        <ul>
                                            <li><a href="#">Event <span>Tournament</span></a></li>
                                            <li><a href="">Event Name<span>King of the Ring</span></a></li>
                                            <li><a href="">Event Location <span>Marietta, Ohio</span></a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                            <div class="col-xl-2">
                                <div class="wrestler-wins-loses">
                                    <div class="event-list">
                                        <ul>
                                            <li class="overall-title"><a href="#">Overall Wrestler</a></li>
                                            <li><a href="">Wins<span>4</span></a></li>
                                            <li><a href="">Losses<span>1</span></a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default WrestlerContext;
