import React, { Component } from 'react'

 class EventWorkerContext extends Component {
    render() {
        return (
            <div>
                <section className="mt-50">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-6">
                                <div className="gototable-select">
                                    <form className="form-inline">
                                        <label className="my-1 mr-2" for="">Select Table</label>
                                        <select className="custom-select my-1 mr-sm-2" id="">
                                        <option selected>Choose...</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                        </select>
                                    </form>
                                    <button  onclick="window.location='FRM14.html';" className="btn btn-primary my-1">Go to Table</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default EventWorkerContext;