import React, { Component } from "react";

class WrestlerPageHeaderTitle extends Component {
  render() {
    return (
      <div>
        <section class="pt-100">
          <div class="container">
            <div class="row main-screen-title blue-screen-title">
              <div class="col-xl-6">
                <div class="w-name">
                  <p>
                    <img
                      src={require("../../assets/images/jhon-doe.jpg")}
                      alt="team name"
                    />
                    <span> Mark Folts</span>
                    <span class="team-name">Team Name : Warriors</span>
                  </p>
                </div>
              </div>
              <div class="col-xl-6">
                <div class="w-werestler-id text-right">
                  <p>Wrestler ID : 012</p>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-xl-2">
                <div class="w-detalis">
                  <ul>
                    <li>Age : 8</li>
                    <li>Weight : 85lb</li>
                    <li>Height : 6' 2"</li>
                  </ul>
                </div>
              </div>
              <div class="col-xl-2">
                <div class="w-detalis">
                  <ul>
                    <li>Wins : 5</li>
                    <li>Losses : 2</li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="row mt-50">
              <div class="col-xl-3">
                <div class="p-attendence">
                  <p>Practice Attendance : 97%</p>
                </div>
              </div>
              <div class="p-attendence-button">
                <button onclick="window.location='FRM7.html';">
                  View Practice Attenance
                </button>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default WrestlerPageHeaderTitle;
