import React, { Component } from 'react'
import Header from '../components/Header'
import ViewEventTitle from '../components/ViewEventComponents/ViewEventTitle'
import ViewEventContext from '../components/ViewEventComponents/ViewEventContext'
import ViewEventTable from '../components/ViewEventComponents/ViewEventTable'
import EditEvent from '../components/ViewEventComponents/EditEvent'

class ViewEvent extends Component {
    render() {
        return (
            <div>
                <Header/>
                <ViewEventTitle/>
                <ViewEventContext/>
                <ViewEventTable/>
                <EditEvent/>
            </div>
        )
    }
}

export default ViewEvent;