import React, { Component } from "react";

class WrestlerTeamHeader extends Component {
  render() {
    return (
      <div>
        <section className="pt-100">
          <div className="container">
            <div className="row main-screen-title blue-screen-title">
              <div className="col-xl-6">
                <div className="w-name">
                  <p>
                    <img
                      src={require("../../assets/images/teamlogo.png")}
                      alt="team name"
                    />
                    <span>My Wrestling Team</span>
                  </p>
                </div>
              </div>
              <div className="col-xl-6">
                <div className="w-werestler-id text-right">
                  <p>Team ID : TMT001</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="w-stats">
          <div className="container">
            <div className="row">
              <div className="col-xl-6">
                <div className="win-lose">
                  <ul>
                    <li>
                      Wins: <span>52</span>
                    </li>
                    <li>
                      Losses: <span>28</span>
                    </li>
                  </ul>
                </div>
              </div>
              <div className="col-xl-6">
                <div className="p-s-button text-right">
                  <ul>
                    <li>
                      <a href="">
                        <img
                          src={require("../../assets/images/icon_practice.svg")}
                          alt=""
                        />
                        View Practice Schedule
                      </a>
                    </li>
                    <li>
                      <a href="">
                        <img
                          src={require("../../assets/images/icon_dollar.svg")}
                          alt=""
                        />
                        View Team Funds
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default WrestlerTeamHeader;
