import React, { Component } from "react";

class WrestlerTeamTable extends Component {
  render() {
    return (
      <div>
        <section className="pt-100">
          <div className="container">
            <div className="row">
              <div className="col-xl-12">
                <div className="coaches">
                  <div className="coaches-title">
                    <h1>
                      <img
                        src={require("../../assets/images/icon_wrestlers.svg")}
                        alt=""
                      />
                      Wrestlers
                    </h1>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-xl-12">
                <div className="wrestlers-table">
                  <table className="table">
                    <thead>
                      <tr>
                        <th scope="col">Name</th>
                        <th scope="col">Age</th>
                        <th scope="col">Weight</th>
                        <th scope="col">Wins</th>
                        <th scope="col">Loses</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr className="space">
                        <th scope="row">
                          <a href="FRM8.html">Jimmy Smith</a>
                        </th>
                        <td>5</td>
                        <td>67</td>
                        <td>3</td>
                        <td>6</td>
                      </tr>
                      <tr>
                        <th scope="row">
                          <a href="FRM8.html">Dyrk Lang</a>
                        </th>
                        <td>5</td>
                        <td>77</td>
                        <td>0</td>
                        <td>26</td>
                      </tr>
                      <tr>
                        <th scope="row">
                          <a href="FRM8.html">Harold Welch</a>
                        </th>
                        <td>6</td>
                        <td>82</td>
                        <td>6</td>
                        <td>16</td>
                      </tr>
                      <tr>
                        <th scope="row">
                          <a href="FRM8.html">Emma Folts</a>
                        </th>
                        <td>5</td>
                        <td>42</td>
                        <td>41</td>
                        <td>0</td>
                      </tr>
                      <tr>
                        <th scope="row">
                          <a href="FRM8.html">Cameron Berry</a>
                        </th>
                        <td>9</td>
                        <td>102 </td>
                        <td>22 </td>
                        <td>18</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default WrestlerTeamTable;
