import React, { Component } from "react";

class ViewEventContext extends Component {
  render() {
    return (
      <div>
        <section className="event-details">
          <div className="container">
            <div className="row event-p">
              <div className="col-xl-4">
                <div className="main-event-details">
                  <ul>
                    <li>Event Name : Lorem Espam</li>
                    <li>Event Location : Lorem Espam</li>
                    <li>Event State : Lorem</li>
                    <li>Event City : Lorem</li>
                    <li>Event Zip Code : 16205</li>
                  </ul>
                </div>
              </div>
              <div className="col-xl-4">
                <div className="overall-team">
                  <ul>
                    <li className="overall-title">Overall Team</li>
                    <li className="w-width">Wins : 13</li>
                    <li className="w-width">Losses : 7</li>
                  </ul>
                </div>
              </div>
              <div className="col-xl-4">
                <div className="event-flier">
                  <img
                    src={require("../../assets/images/frm3img.png")}
                    alt="event flier"
                  />
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default ViewEventContext;
